import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";

const API_BASE = "http://127.0.0.1:8000";

export default function App() {
  const [resumes, setResumes] = useState([]);
  const [file, setFile] = useState(null);
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(false);

  async function fetchResumes() {
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/resumes`);
      const data = await res.json();
      setResumes(data);
    } catch (err) {
      console.error("Failed to fetch resumes:", err);
    }
    setLoading(false);
  }

  async function uploadResume(e) {
    e.preventDefault();
    if (!file) return;
    const formData = new FormData();
    formData.append("file", file);
    setLoading(true);
    await fetch(`${API_BASE}/upload`, { method: "POST", body: formData });
    setFile(null);
    await fetchResumes();
  }

  useEffect(() => {
    fetchResumes();
  }, []);

  const filtered = resumes.filter((r) =>
    r.name?.toLowerCase().includes(query.toLowerCase())
  );

  return (
    <div className="p-6 max-w-5xl mx-auto">
      <motion.h1
        className="text-4xl font-bold text-center mb-8 text-purple-700"
        initial={{ y: -20, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
      >
        Resume<span className="text-indigo-600">RAG</span> — Smart Resume Matcher
      </motion.h1>

      {/* Upload Section */}
      <div className="bg-white shadow-xl rounded-2xl p-6 mb-8">
        <form onSubmit={uploadResume} className="flex flex-col sm:flex-row items-center gap-4">
          <input
            type="file"
            accept=".pdf,.doc,.docx"
            onChange={(e) => setFile(e.target.files[0])}
            className="border border-gray-300 rounded-lg px-4 py-2 w-full"
          />
          <button
            type="submit"
            disabled={loading}
            className="bg-purple-600 text-white px-6 py-2 rounded-lg hover:bg-purple-700 transition-all"
          >
            {loading ? "Uploading..." : "Upload Resume"}
          </button>
        </form>
      </div>

      {/* Search Box */}
      <div className="mb-6 flex justify-center">
        <input
          type="text"
          placeholder="Search resumes by name..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          className="w-full sm:w-2/3 px-4 py-2 border rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-purple-400"
        />
      </div>

      {/* Resume Cards */}
      {loading ? (
        <p className="text-center text-gray-500">Loading...</p>
      ) : filtered.length === 0 ? (
        <p className="text-center text-gray-400">No resumes found.</p>
      ) : (
        <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {filtered.map((r, i) => (
            <motion.div
              key={i}
              className="p-5 bg-white rounded-xl shadow-lg hover:shadow-2xl transition"
              whileHover={{ scale: 1.03 }}
            >
              <h2 className="text-lg font-semibold text-gray-800 mb-2">
                {r.name || `Candidate ${i + 1}`}
              </h2>
              <p className="text-gray-600 text-sm mb-4">{r.skills || "Skills: Not Extracted"}</p>
              <button className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700">
                View Details
              </button>
            </motion.div>
          ))}
        </div>
      )}
    </div>
  );
}
